const uni = require('uni').default;

































































































































































import server from '../../../store/server.js'
var sourceType = [
	['camera'],
	['album'],
	['camera', 'album']
]
var sizeType = [
	['compressed'],
	['original'],
	['compressed', 'original']
]
export default {
	data() {
		return {
			title: 'picker',
			companyList: ['请选择分公司'],				
			projectList:['请选择项目'],
			areaList:['请选择区域'],
			unitList:['请选择施工单位'],
			dangerList:['请选择隐患类型'],
			dangerDesList:['请选择隐患描述'],
			rectiList:['请选择整改人'],
			noticeList:['请选择通知人'],
			degreeList:['一般','紧急','严重'],
			companyVal:0,
			projectVal: 0,
			areaVal:0,
			unitVal:0,
			dangerVal:0,
			dangerDesVal:0,
			degreeVal:0,
			checkDate: '2018-09-18',
			recheckDate: '2018-10-18',
			deadline:'2018-09-21',
			time: '12:01',
			imageList: [],
			sourceTypeIndex: 2,
			sourceType: ['拍照', '相册', '拍照或相册'], 
			sizeTypeIndex: 2,
			sizeType: ['压缩', '原图', '压缩或原图'],
			countIndex: 8,
			count: [1, 2, 3, 4, 5, 6, 7, 8, 9]
		}
	},
	onLoad(){
		console.log("页面加载ing")
		this.getCompany();
		this.getProject();
	},
	
	onUnload(){
		this.imageList = [],
		this.sourceTypeIndex= 2,
		this.sourceType= ['拍照', '相册', '拍照或相册'],
		this.sizeTypeIndex= 2,
		this.sizeType= ['压缩', '原图', '压缩或原图'],
		this.countIndex= 8;
		
	}, 
	methods: { 			
		chooseImage: async function () {  //选择图片
			if(this.imageList.length === 9){
				let isContinue = await this.isFullImg();
				console.log("是否继续?",isContinue);
				if(!isContinue){
					return;
				}
			}
			uni.chooseImage({ //调用图片选择API
				sourceType: sourceType[this.sourceTypeIndex],

				sizeType: sizeType[this.sizeTypeIndex],

				count: this.imageList.length + this.count[this.countIndex] > 9 ? 9 - this.imageList.length : this.count[this.countIndex],
				success: (res) => {
					this.imageList = this.imageList.concat(res.tempFilePaths);
				}
			})
		},
		isFullImg:function(){  //判断图片最大数量
			return new Promise((res) => {
				uni.showModal({
					content:"已经有9张图片了,是否清空现有图片？",
					success: (e) => {
						if(e.confirm){
							this.imageList = [];
							res(true);
						}else{
							res(false)
						}
					},
					fail:() => {
						res(false)
					}
				});
			});
		},
		previewImage: function (e) { //预览图片
			var current = e.target.dataset.src
			uni.previewImage({
				current: current,
				urls: this.imageList
			})
		},
		getCompany(){	 //获取公司数据				
			let url=server.net.setUrl('api/DropDownListApi/GetCompany');
			uni.request({
				url:url,
				dataType:'json',
				type:'GET',
				header:{
					token:server.net.GetSessionToken()
					//token:"cKVZOYoZrMhK88rPLjODLRej0Z%2f5M8S9DvJgzJUMIE4wgTZga9sgHtEi4ABdz2CA"
				},
				success:(data)=>{
					var jsondata=data.data;
					if (jsondata.status = 1) {
						console.log("公司获取成功");
						this.companyList=this.setData(jsondata.data);
						//this.getProject();
					} else{
						console.log(jsondata.status);
						uni.showToast({
							title: jsondata.status,
							mask: false,
							duration: 1500
						});
					}
				},
				fail:(data,code)=>{
					console.log("公司获取失败" + data + code);
				}
			});
		},
		getProject(){
			let url=server.net.setUrl('api/DropDownListApi/GetProject');
			uni.request({
				url: url,
				method: 'GET',
				header: {
					token:server.net.GetSessionToken(),
					company:1
				},  
				success: (data) => {
					var jsondata=data.data;
					if (jsondata.status=1) {
						console.log("项目获取成功");
						this.projectList=this.setData(jsondata.data);
					} else{
						console.log("项目获取失败"+jsondata.status);
					}
				},
				fail: (data,code) => {
					console.log("项目数据请求失败"+data+code)
				},
				complete: () => {}
			});
		},
		setData:(items)=>{
			let newItems=[];
			items.forEach((e) => {
				newItems.push({
					text:e.text,
					value:e.value
				});
			});
			return newItems;
		},
		changeCompany(e) {  //当picker改变时，触发此方法
			console.log('picker发送选择改变，携带值为'+ e.detail.value)
			this.companyVal = e.detail.value;
			this.getProject();
		},
		changeProject(e){
			this.projectVal=e.detail.value;
		},
		changeArea(e){
			this.areaVal=e.detail.value;
		},changeUnit(e){
			this.unitVal=e.detail.value;
		},
		changeDanger(e){
			this.dangerVal=e.detail.value;
		}
	}
}



